
.line-heading {
  font-size: 1.2em;
  border-bottom: solid #999 1px;
  text-align: right;
}
.line-heading .line-label {
    font-size: 1.2em;
    text-transform: capitalize;
    font-weight: 100;
    text-align: left;
}
.line-total {
  border-top: solid #CCC 1px;
}
.line-total > * {
    border-right: solid #CCC 1px;
}
.line {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.line > * {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 0px;
            flex: 1 0 0;
    padding: 2px 4px;
}
.line .duration {
    color: #0b58a2;
}
.line .duration.nothing {
      color: #8f97a0;
}
.line small {
    text-align: center;
    color: rgba(0, 0, 0, 0.25);
}
.line .line-label {
    -webkit-box-flex: 3;
        -ms-flex: 3 0 0px;
            flex: 3 0 0;
}
.line .empty-period {
    -webkit-box-flex: 10;
        -ms-flex: 10 1 0px;
            flex: 10 1 0;
    text-align: center;
}
.line .duration {
    text-align: right;
}
.line-label {
  padding-left: 1em;
}
.line-datas:nth-child(even) {
  background: rgba(0, 0, 0, 0.05);
}
.line-datas:hover {
  background: rgba(11, 88, 162, 0.25);
}
.line-datas > * {
  border-right: solid #CCC 1px;
}



